.header__link {
    color: #fff;
    text-align: right;
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px; /* 122.222% */
    text-decoration: none;
    margin-top: 6px;
    align-self: center;
  }
  
  @media screen and (max-width: 767px) {
    .header__link {
      font-size: 14px;
      margin-right: 27px;
      align-self: start;
    }
  }